import {SaveTeacherEvaluate} from '@/api/teacher'
import SunuiStar from '@/components/uni/sunui-star/sunui-star.vue'
export default {
	data(){
		return{
			loaded:false,
			evaluationData:{},
			starConfig: {
				defaultStar: 0,
				maxStar: 5,
				starSize: '1em',
				isTips: true,
			},
			form:{
				tcid:0,
				content:'',
				star:'',
				type:1
			},
			tname:''
		}
	},
	components:{
		SunuiStar
	},
	onLoad: function(option) {
		console.log(option.tcid)
		this.form.tcid = option.tcid
		this.tname = option.tname
		this.loaded = true
	},
	methods: {
		conInput(e){
			this.form.content = e.detail.value
		},
		changeStar(e){
			this.form.star = e.curStar
		},
		submit(){
			uni.showLoading({
			    title: '正在提交中'
			});
			SaveTeacherEvaluate(this.form).then(res=>{
				uni.hideLoading();
				if(res.result){
					uni.showToast({
					    title: '评价成功',
					    duration: 1500,
						icon:'none'
					});
					setTimeout(function () {
					    uni.navigateBack()
					}, 1500);
				}else{
					uni.showToast({
					    title: res.msg,
					    duration: 1500,
						icon:'none'
					});
				}
			})
		}
	}
}
